const BASE_URL = 'https://api.pokemontcg.io/v2';

// Update signature to accept filters
export async function searchCards(query = '', page = 1, pageSize = 20, filters = {}) {
    try {
        // Construct local proxy URL
        // We pass the raw query string here. The proxy will forward it.

        // Build parts of the Lucene query
        const queryParts = [];

        if (query) {
            // Name search (prefix match for better UX and performance)
            // Docs: name:char* matches cards starting with char.
            // Wrapping in quotes handles spaces.
            queryParts.push(`name:"${query}*"`);
        }

        // Add Set Name Filter if present
        if (filters.setName) {
            queryParts.push(`set.name:"*${filters.setName}*"`);
        }

        // Add Type Filter if present
        if (filters.types) {
            queryParts.push(`types:"*${filters.types}*"`);
        }

        // Add Rarity Filter
        if (filters.rarity) {
            // Rarity often has spaces like "Rare Holo"
            queryParts.push(`rarity:"${filters.rarity}"`);
        }

        // Join all parts with space (AND)
        const luceneQuery = queryParts.join(' ');

        // Handle Sort (orderBy)
        // If "relevance", we don't send orderBy (API defaults? or we just don't send it)
        const queryParams = {
            page: page.toString(),
            pageSize: pageSize.toString(),
        };

        if (filters.orderBy && filters.orderBy !== 'relevance') {
            queryParams.orderBy = filters.orderBy;
        }

        const params = new URLSearchParams(queryParams);

        if (luceneQuery) {
            params.append('q', luceneQuery);
        }

        const url = `/api/proxy/cards?${params.toString()}`;

        console.log(`[API Request] Fetching Proxy: ${url}`);

        const response = await fetch(url);

        console.log(`[API Response] Status: ${response.status}`);

        if (!response.ok) {
            throw new Error(`API Error: ${response.status}`);
        }

        const data = await response.json();
        return {
            data: data.data,
            page: data.page,
            pageSize: data.pageSize,
            count: data.count,
            totalCount: data.totalCount
        };
    } catch (error) {
        console.error("Failed to fetch cards:", error);
        return { data: [], error: error.message };
    }
}

